import con from './config'

const state = {
  nature: [],
  currentRowNo: 0
}

const getters = {
  allNature: state => state.nature,
  ngetCurrentrow: state => state.currentRowNo,
}

const actions = {

  async fetchNatureList({ commit }, row) {
    await con.get(`/Nature/${row.limit}/${row.offset}`).then(function (res) {
      if (res.data['Nature'].length > 0) {
        // commit data until last
        commit('getNature', res.data['Nature']);
      } else {
        // minus 10 if last data meet
        var rowoffset = row.offset - row.limit;
        if (rowoffset <= 0) {
          rowoffset = 0;
        }
        commit('currentRow', rowoffset);
      }

    }).catch(error => {
      console.log(error);
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  async increment({ commit }, row) {
    var limitoffset = parseInt(row.offset) + parseInt(row.limit);
    commit('currentRow', limitoffset);
  },
  async decrement({ commit }, row) {
    // decrement
    var limitoffset = parseInt(row.offset) - parseInt(row.limit);
    commit('currentRow', limitoffset);
    if (row.offset <= 0) {
      // commit to 0 if data is less than zero
      commit('currentRow', 0);
    }
  },
  async searchNatureList({ commit }, res) {
    var limit = parseInt(res.limit);
    await con.post('/SearchNatureList', {
      txtsearch: res.txtsearch,
      txtlimit: limit
    }).then(function (res) {
      commit('currentRow', res.data['offset']);
      commit('getNature', res.data['NatureList']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  async CreateNatureClass({ commit }, res) {
    await con.post(`/CreateNatureClass/${res.limit}/${res.offset}`,
      {
        nature_of_business: res.class
      }).then(function (res) {
        commit('getNature', res.data['Nature']);
        swal({
          title: "Nature of Business",
          text: "Save Successfully",
          icon: "success",
          button: true,
        });
      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        } else {
          var errorLog = JSON.stringify(error.response.data);
          swal({
            title: "Server Side Validation",
            text: errorLog,
            icon: "error",
            dangerMode: true,
          });
        }
      })
  },
  async UpdateNatureClass({ commit }, val) {
    const res = await con.put(`/UpdateNatureClass/${val.id}`, {
      nature_of_business: val.class
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      } else {
        var errorLog = JSON.stringify(error.response.data);
        swal({
          title: "Server Side Validation",
          text: errorLog,
          icon: "error",
          dangerMode: true,
        });
      }

    })
    commit('getNature', res.data['Nature']);
  },

  async deleteNatureList({ commit }, res) {
    const result = await con.delete(`/DeleteNatureClass/${res.id}/${res.limit}/${res.offset}`).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
    commit('getNature', result.data['Nature']);
  },


};

const mutations = {
  getNature: (state, Land) => (state.nature = Land),
  currentRow: (state, Irow) => (state.currentRowNo = Irow),
}

export default {
  state,
  getters,
  actions,
  mutations
};