import con from './config'
import swal from 'sweetalert'
const state = {
  responsibility: [],
  currentRowNo: 0,
  fmodules: []
}

const getters = {
  allRes: state => state.responsibility,
  rgetCurrenrow: state => state.currentRowNo,
  allModule: state => state.fmodules
}

const actions = {

  async fetchRes({ commit }, row) {
    await con.get(`/Responsibility/${row.limit}/${row.offset}`).then(function (res) {
      if (res.data['Res'].length > 0) {
        // commit data until last
        commit('getRes', res.data['Res']);
      } else {
        // minus limit if last data meet
        var rowoffset = row.offset - row.limit;
        if (rowoffset <= 0) {
          rowoffset = 0;
        }
        commit('currentRow', rowoffset);
      }

    }).catch(error => {
      console.log(error);
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async fetchModules({ commit }) {
    await con.get(`/Modules`).then(function (res) {
      var module = [];
      for (let i = 0; i < res.data['Modules'].length; i++) {
        module[i] = {
          value: res.data['Modules'][i]['title'],
          text: res.data['Modules'][i]['title']
        }
      }
      commit('getMods', module);

    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async CreateRes({ commit }, res) {

    var listmodule = [];

    for (let i = 0; i < res.modules.length; i++) {
      listmodule += [res.modules[i]['value'] + ","];
    }

    listmodule = listmodule.slice(0, -1);
    // listmodule = "["+listmodule+"]";

    const result = await con.post(`/Responsibility/${res.limit}/${res.offset}`,
      {
        access_level: res.access_level,
        modules: listmodule,
      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        } else {
          var errorLog = JSON.stringify(error.response.data);
          swal({
            title: "Server Side Validation",
            text: errorLog,
            icon: "error",
            dangerMode: true,
          });
        }
      })
    commit('getRes', result.data);
  },

  async updateRess({ commit }, res) {

    var listmodule = [];

    for (let i = 0; i < res.modules.length; i++) {
      listmodule += [res.modules[i] + ","];
    }

    listmodule = listmodule.slice(0, -1);

    const result = await con.put(`/Responsibility/${res.id}`, {
      access_level: res.access_level,
      modules: listmodule
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
    commit('getRes', result.data['Res']);
  },

  async deleteRess({ commit }, res) {
    const result = await con.delete(`/Responsibility/${res.limit}/${res.offset}/${res.id}`).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
    commit('getRes', result.data['Res']);
  },

  async searchRes({ commit }, res) {
    var limit = parseInt(res.limit);
    await con.post('/SearchRes', {
      txtsearch: res.txtsearch,
      txtlimit: limit
    }).then(function (res) {
      commit('currentRow', res.data['offset']);
      commit('getRes', res.data['Res']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async increment({ commit }, row) {
    var limitoffset = parseInt(row.offset) + parseInt(row.limit);
    commit('currentRow', limitoffset);
  },
  async decrement({ commit }, row) {
    // decrement
    var limitoffset = parseInt(row.offset) - parseInt(row.limit);
    commit('currentRow', limitoffset);
    if (row.offset <= 0) {
      // commit to 0 if data is less than zero
      commit('currentRow', 0);
    }
  },


};

const mutations = {
  getRes: (state, Res) => (state.responsibility = Res),
  currentRow: (state, Irow) => (state.currentRowNo = Irow),
  getMods: (state, Mods) => (state.fmodules = Mods),
  // CreateRes: (state, NewRes) => state.responsibility.unshift(NewRes),
}

export default {
  state,
  getters,
  actions,
  mutations
};